@import url(https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,500;1,200;1,400&display=swap);
body {
  margin: 0;
  font-family: "Poppins", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  box-sizing: border-box;
}
:root {
  --main-bg-color: brown;
  --txtColor: rgb(180, 180, 180);
  --initBackground:linear-gradient(to right bottom, #201b23, #221f28, #24242d, #262832, #282d36, #282c35, #272c33, #272b32, #23252b, #1f1f24, #1a1a1d, #151416);
  --doBackground: linear-gradient(
    to left top,
    #3f649d,
    #47568a,
    #494977,
    #483d64,
    #433252,
    #423150,
    #42304f,
    #412f4d,
    #45385b,
    #47416a,
    #464b78,
    #435587
  );
}

.App {
  text-align: center;
  box-sizing: border-box;
  margin: 0;
}



.Home {
  box-sizing: border-box;
  background: var(--initBackground);
  min-height: 100vh;
  color: white;
  margin: 0;
  -webkit-animation: luminusBackground 10s ease-in-out infinite;
          animation: luminusBackground 10s ease-in-out infinite;
  background-size: 400% 300%;
}
@-webkit-keyframes luminusBackground {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 75%;
  }

  100% {
    background-position: 0% 50%;
  }
}
@keyframes luminusBackground {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 75%;
  }

  100% {
    background-position: 0% 50%;
  }
}

.hero {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  text-align: center;
}
h1 {
  color: #009fb7;
  font-size: 3rem;
  font-weight: 500;
}
h6 {
  color: #fed766;
  font-size: 1.6rem;
  font-weight: 400;
  margin: 0;
}
p {
  text-align: center;
  font-weight: 100;
  font-size: 1rem;
}
a {
  width: 40%;
}
ul {
  list-style: none;
  padding-left: 0px;
}
li {
  font-weight: 200;
}
main {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

main .avatar {
  width: 100px;
  height: 100px;
  border-radius: 50%;
  object-fit: fill;
  border: none;
  background-image: url(/portafolio-2021/static/media/avatar.0087e962.jpg);
  background-size: cover;

  box-shadow: 2px 2px 10px black;
  border: 1px solid rgb(82, 82, 82);
}
.container {
  width: 100%;
  height: 100%;
  padding-top: 3rem;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
}

.subtitle {
  color: rgb(206, 205, 205);
  font-weight: 100;
  text-align: center;
}
.subtitle b {
  color: #66fe8b;
}
.estudio {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  width: 100%;
  margin-top: 1rem;
  max-width: 2000px;
}
.info {
  width: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
}
.info p {
  margin-left: 1rem;
  margin-right: 1rem;
}

.subContainer {
  width: 100%;
  max-width: 600px;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  margin-bottom: 1rem;
  padding-left: 3rem;
  padding-right: 3rem;
  padding-bottom: 3rem;
}

.redes {
  text-align: center;
  width: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  margin-top: 3rem;
