  margin-right: 1rem;
}
.redes div {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  padding: 1.6rem;
}
.redes img {
  width: 35%;
  max-width: 300px;
  object-fit: cover;
}

.redes div div {
  height: minmax(300px, 100%);
  height: -webkit-min-content;
  height: min-content;
}

#instagram,
#twitter,
#linkedin {
  padding: 1rem;
  width: 70%;
  max-width: 200px;
  text-align: center;
  -webkit-align-items: center;
          align-items: center;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
}
#cvBtn {
  position: fixed;
  right: 100px;
  top: 50px;
  color: white;
  padding: 1rem;
  font-size: 0.8rem;
  background-color: #e4e4e400;
  color: white;
  font-weight: 800;
  border-radius: 24px;
  border: #fed766 1px solid;
}

#cvBtn:hover {
  -webkit-animation-duration: 1s;
          animation-duration: 1s;
  -webkit-animation-name: luminus;
          animation-name: luminus;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
  box-shadow: 3px 4px 8px black;
}

@-webkit-keyframes luminus {
  from {
    color: white;
    background-color: #fed8664b;
  }

  to {
    color: #1d2729;
    background: #fed766;
  }
}

@keyframes luminus {
  from {
    color: white;
    background-color: #fed8664b;
  }

  to {
    color: #1d2729;
    background: #fed766;
  }
}

@media (max-width: 600px) {
  #cvBtn {
    position: absolute;
    right: 10px;
    top: 20px;
    width: 100px;
    height: 50px;
    font-size: 0.6rem;
  }
  .hero {
    -webkit-justify-content: space-evenly;
            justify-content: space-evenly;
    -webkit-align-items: center;
            align-items: center;
    margin-bottom: 1rem;
  }

  h1 {
    margin-bottom: 0;
    font-size: 1.6rem;
  }
  .subContainer {
    padding: 1rem;
  }
}
#btnPortafolio {
  background-color: rgba(255, 222, 173, 0);
  border: 1px solid #009fb7;
