  margin-top: 1rem;
  font-size: 1rem;
  width: 100%;
  padding: 0.6rem;
  border-radius: 8px;
  max-width: 300px;
}

#btnCursos:hover {
  -webkit-animation-duration: 1s;
          animation-duration: 1s;
  -webkit-animation-name: luminusCursos;
          animation-name: luminusCursos;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
  box-shadow: 3px 4px 8px black;
}
@-webkit-keyframes luminusCursos {
  from {
    background-color: #00000000;
  }

  to {
